import axios from 'axios'

const USUARIOS_REST_API_URL = 'http://localhost:8080/api/v1/usuarios';

class UsuarioService {

    getUsuarios(headers) {
        return axios.get(USUARIOS_REST_API_URL,headers);
    }

    saveUsuario(headers, usuario) {
        return axios.post(USUARIOS_REST_API_URL, usuario, headers)
    }

    getUsuarioById(headers, idUsuario) {
        return axios.get(USUARIOS_REST_API_URL + '/' + idUsuario, headers)
    }
    
    updateUsuario(headers, idUsuario, usuario) {
        return axios.put(USUARIOS_REST_API_URL + '/' + idUsuario, usuario, headers)
    }
    cadastrar(usuario) {
        return axios.post(USUARIOS_REST_API_URL + "/cadastro", usuario)
    }
}

export default new UsuarioService();
