import axios from 'axios'


const POPULACAO_APRENDIZAGEM_REST_API_URL = 'http://localhost:8080/api/v1/populacao';

class PopulacaoService {

    getPopulacao(header,municipio, sort) {
        let params = ""
        if (municipio) {
            params = "?m="+municipio
        }
        if (sort) {
            let sinal = "?";
            if (municipio) {
                sinal = "&"
            }
            
            params += sinal +"sort="+sort
        }
        return axios.get(POPULACAO_APRENDIZAGEM_REST_API_URL+params, header);
    }

    deletePopulacao(header) {
        return axios.delete(POPULACAO_APRENDIZAGEM_REST_API_URL, header);
    }

}

export default new PopulacaoService();