import React, { useEffect, useState } from 'react'
import { styled } from '@mui/material/styles';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import UploadService from '../services/UploadFileService';
import { Alert, AlertTitle } from '@mui/material';
import UploadFileService from '../services/UploadFileService';
import CotasAprendizagemService from '../services/CotasAprendizagemService';
import DesocupacaoService from '../services/DesocupacaoService';
import PopulacaoService from '../services/PopulacaoService';
import IdhmService from '../services/IdhmService';
import useToken from '../useToken';



const Input = styled('input')({
    display: 'none',
  });

const Uploads = () => {

    const [mensagemCotas, setMensagemCotas] = useState({
        tipo: "",
        mensagemRetorno: ""
    });

    const limparMensagemCotas = e => {
        setMensagemCotas({
            tipo: "",
            mensagemRetorno: ""
        })
    } ;

    const limparMensagemTxDesocupacao = e => {
        setMensagemTxDesocupacao({
            tipo: "",
            mensagemRetorno: ""
        })
    } ;
    const limparMensagemPopulacao = e => {
        setMensagemPopulacao({
            tipo: "",
            mensagemRetorno: ""
        })
    } ;
    const limparMensagemIdhm = e => {
        setMensagemIdhm({
            tipo: "",
            mensagemRetorno: ""
        })
    } ;

    const [mensagemPopulacao, setMensagemPopulacao] = useState({
        tipo: "",
        mensagemRetorno: ""
    });
    const [mensagemTxDesocupacao, setMensagemTxDesocupacao] = useState({
        tipo: "",
        mensagemRetorno: ""
    });
    const [mensagemIdhm, setMensagemIdhm] = useState({
        tipo: "",
        mensagemRetorno: ""
    });

    const [fileCotas, setFileCotas] = useState('');
    const [uriFileCotas, setUriFileCotas] = useState('');

    
    const uploadFileCotas = async e => {

        setFileCotas(e.target.files[0])
        e.preventDefault();
        console.log("Upload COTAS");

    };

    const token = useToken();    

    let header = { headers : {
        Authorization: `Bearer ${token.token}`
        }
    };

    useEffect(() => {
        if (fileCotas) {
            const formData = new FormData();
            formData.append('file', fileCotas);

            UploadService.uploadFile(header, formData)
            .then((response) => {
                console.log(response.data.mensagemRetorno);
                setUriFileCotas(response.data.downloadUri)
                setMensagemCotas({
                    tipo: response.data.tipoRetorno,
                    mensagemRetorno: response.data.mensagemRetorno
                })
            
            }).catch((err) => {
                console.log(err.response.data.mensagemRetorno);
            })
        }
    }, [fileCotas]);

    const processarCotas = async e => {
        console.log(uriFileCotas)
        const headers = {
            'headers': {
                'Content-type': 'application/json',
                'type-upload': 'COTAS',
                'Authorization': `Bearer ${token.token}`
            }
            
        }
        UploadFileService.processarFile(uriFileCotas, headers).then((response) => {
            setMensagemCotas({
                tipo: response.data.tipoRetorno,
                mensagemRetorno: response.data.mensagemRetorno
            })
        })

    }

    const zerarCotas = async e => {
        
        CotasAprendizagemService.deleteCotasAprendizagem(header).then((response) => {
            setMensagemCotas({
                tipo: "SUCESSO",
                mensagemRetorno: "Tabela Cotas de Aprendizagem Zerada!"
            })
        })

    }

    

    const [fileTxDesocupacao, setFileTxDesocupacao] = useState('');
    const [uriFileTxDesocupacao, setUriFileTxDesocupacao] = useState('');
    
    const uploadTxDesocupacao = async e => {
        setFileTxDesocupacao(e.target.files[0])
        e.preventDefault();
        console.log("Upload Taxa de Desocupação");
    }

    useEffect(() => {
        if (fileTxDesocupacao) {
            const formData = new FormData();
            formData.append('file', fileTxDesocupacao);

            UploadService.uploadFile(header, formData)
            .then((response) => {
                console.log(response.data.mensagemRetorno);
                setUriFileTxDesocupacao(response.data.downloadUri)
                setMensagemTxDesocupacao({
                tipo: response.data.tipoRetorno,
                mensagemRetorno: response.data.mensagemRetorno
            })
        
            }).catch((err) => {
                console.log(err.response.data.mensagemRetorno);
            })
        }
    }, [fileTxDesocupacao]);

    const processarTxDesocupacao = async e => {
        console.log(uriFileTxDesocupacao)
        const headers = {
            'headers': {
                'Content-type': 'application/json',
                'type-upload': 'TX-DESOCUPACAO',
                'Authorization': `Bearer ${token.token}`
            }
        }
        UploadFileService.processarFile(uriFileTxDesocupacao, headers).then((response) => {
            setMensagemTxDesocupacao({
                tipo: response.data.tipoRetorno,
                mensagemRetorno: response.data.mensagemRetorno
            })
        })

    };

    const zerarTxDesocupacao = async e => {
        
        DesocupacaoService.deleteTxDesocupacao(header).then((response) => {
            setMensagemTxDesocupacao({
                tipo: "SUCESSO",
                mensagemRetorno: "Tabela de Taxa de Desocupação Zerada!"
            })
        })

    }
    
    const [filePopulacao, setFilePopulacao] = useState('');
    const [uriFilePopulacao, setUriFilePopulacao] = useState('');
    
    const uploadPopulacao = async e => {
        setFilePopulacao(e.target.files[0])
        e.preventDefault();
        console.log("Upload População");
    }

    useEffect(() => {
        if (filePopulacao) {
            const formData = new FormData();
            formData.append('file', filePopulacao);

            UploadService.uploadFile(header, formData)
            .then((response) => {
                console.log(response.data.mensagemRetorno);
                setUriFilePopulacao(response.data.downloadUri)
                setMensagemPopulacao({
                    tipo: response.data.tipoRetorno,
                    mensagemRetorno: response.data.mensagemRetorno
                })
        
        }).catch((err) => {
            console.log(err.response.data.mensagemRetorno);
        })
        }
    }, [filePopulacao]);

    const processarPopulacao = async e => {
        console.log(uriFilePopulacao)
        const headers = {
            'headers': {
                'Content-type': 'application/json',
                'type-upload': 'POPULACAO',
                'Authorization': `Bearer ${token.token}`
            }
        }
        UploadFileService.processarFile(uriFilePopulacao, headers).then((response) => {
            setMensagemPopulacao({
                tipo: response.data.tipoRetorno,
                mensagemRetorno: response.data.mensagemRetorno
            })
        })

    };

    const zerarPopulacao = async e => {
        
        PopulacaoService.deletePopulacao(header).then((response) => {
            setMensagemPopulacao({
                tipo: "SUCESSO",
                mensagemRetorno: "Tabela de População Zerada!"
            })
        })

    }


    const [fileIdhm, setFileIdhm] = useState('');
    const [uriFileIdhm, setUriFileIdhm] = useState('');

    const uploadIdhm = async e => {
        setFileIdhm(e.target.files[0])
        e.preventDefault();
        console.log("Upload IDHM");
        
    };

    useEffect(() => {
        if (fileIdhm) {
            const formData = new FormData();
            formData.append('file', fileIdhm);

            UploadService.uploadFile(header, formData)
            .then((response) => {
                console.log(response.data.mensagemRetorno);
                setUriFileIdhm(response.data.downloadUri)
                setMensagemIdhm({
                    tipo: response.data.tipoRetorno,
                    mensagemRetorno: response.data.mensagemRetorno
                })
            
            }).catch((err) => {
                console.log(err.response.data.mensagemRetorno);
            })
        }

    }, [fileIdhm]);

    const processarIdhm = async e => {
        console.log(uriFileIdhm)
        const headers = {
            'headers': {
                'Content-type': 'application/json',
                'type-upload': 'IDHM',
                'Authorization': `Bearer ${token.token}`
            }
        }
        UploadFileService.processarFile(uriFileIdhm, headers).then((response) => {
            setMensagemIdhm({
                tipo: response.data.tipoRetorno,
                mensagemRetorno: response.data.mensagemRetorno
            })
        })

    };

    const zerarIdhm = async e => {
        
        IdhmService.deleteIdhm(header).then((response) => {
            setMensagemIdhm({
                tipo: "SUCESSO",
                mensagemRetorno: "Tabela de IDHM Zerada!"
            })
        })

    };

    return (
        <div>
            <h2 className="page-header">
                Uploads
            </h2>
            <div className='container'>
                <div className='row'>
                    <div className='card col-12'>
                        <div className='card-body'>
                            <form className="line-upload" id='form-fileCotas'>
                                <div className='upload'> 
                                    <label className='col-3' htmlFor="">Cotas de Aprendizagem por Segmento:</label>
                                    <div className='col-1'>
                                        <label htmlFor="contained-button-fileCotas">
                                            <Input accept=".csv" 
                                                id="contained-button-fileCotas"  
                                                type="file" 
                                                name="fileCotas"
                                                onChange={uploadFileCotas}   
                                            />
                                            <IconButton variant="contained" component="span">                                            
                                                <i class='bx bx-upload'></i>
                                            </IconButton>
                                        </label>
                                        
                                    </div>                 
                                    <div className='col-4'>                                    
                                        <div className='row button-primary'>
                                            <Button variant="contained" 
                                                onClick={processarCotas} 
                                                className='button-cadastrar'>
                                                Processar
                                            </Button>
                                        </div>   
                                    </div>  
                                    <div className='col-4'>                                    
                                        <div className='row button-primary'>
                                            <Button variant="outlined" className="zerar-button"
                                                onClick={zerarCotas} >                                                
                                                Zerar
                                            </Button>    
                                        </div>   
                                    </div>                  
                                </div>
                                <div className='upload-feedback'> 
                                    <div className="file-name-upload col-3">
                                        <label className=''>{fileCotas.name}</label>
                                    </div>
                                    <div className="col-8">
                                        {mensagemCotas.tipo === 'SUCESSO' ? <Alert 
                                            onClose={limparMensagemCotas} severity="success">
                                            <AlertTitle>{mensagemCotas.mensagemRetorno}</AlertTitle>
                                        </Alert>
                                        :""}

                                        {mensagemCotas.tipo === 'ERRO' ? <Alert 
                                            onClose={limparMensagemCotas} severity="error">
                                            <AlertTitle>{mensagemCotas.mensagemRetorno}</AlertTitle>
                                        </Alert>
                                        :""}
                                    </div>
                                </div>
                            </form>

                            <form className="line-upload">
                                <div className='upload'> 
                                    <label className='col-3' htmlFor="">Taxa de Desocupação:</label>
                                    <div className='col-1'>
                                        <label htmlFor="contained-button-fileTxDesocupacao">
                                            <Input accept=".csv" 
                                                id="contained-button-fileTxDesocupacao"  
                                                type="file" 
                                                name="fileTxDesocupacao"
                                                onChange={uploadTxDesocupacao}   
                                            />
                                            <IconButton variant="contained" component="span">                                            
                                                <i class='bx bx-upload'></i>
                                            </IconButton>
                                        </label>
                                        
                                    </div>                 
                                    <div className='col-4'>                                    
                                        <div className='row button-primary'>
                                            <Button variant="contained" 
                                                onClick={processarTxDesocupacao} 
                                                className='button-cadastrar'>
                                                Processar
                                            </Button>
                                        </div>   
                                    </div>    
                                    <div className='col-4'>                                    
                                        <div className='row button-primary'>
                                            <Button variant="outlined" className="zerar-button"
                                                onClick={zerarTxDesocupacao} >                                                
                                                Zerar
                                            </Button>    
                                        </div>   
                                    </div>                 
                                </div>
                                <div className='upload-feedback'> 
                                    <div className="file-name-upload col-3">
                                        <label className=''>{fileTxDesocupacao.name}</label>
                                    </div>
                                    <div className="col-8">
                                        {mensagemTxDesocupacao.tipo === 'SUCESSO' ? <Alert 
                                            onClose={limparMensagemTxDesocupacao} severity="success">
                                            <AlertTitle>{mensagemTxDesocupacao.mensagemRetorno}</AlertTitle>
                                        </Alert>
                                        :""}

                                        {mensagemTxDesocupacao.tipo === 'ERRO' ? <Alert 
                                            onClose={limparMensagemTxDesocupacao} severity="error">
                                            <AlertTitle>{mensagemTxDesocupacao.mensagemRetorno}</AlertTitle>
                                        </Alert>
                                        :""}
                                    </div>
                                </div>
                            </form>
                            <form className="line-upload">
                                <div className='upload'> 
                                    <label className='col-3' htmlFor="">População entre 15 e 24 anos:</label>
                                    <div className='col-1'>
                                        <label htmlFor="contained-button-filePopulacao">
                                            <Input accept=".csv" 
                                                id="contained-button-filePopulacao"  
                                                type="file" 
                                                name="filePopulacao"
                                                onChange={uploadPopulacao}   
                                            />
                                            <IconButton variant="contained" component="span">                                            
                                                <i class='bx bx-upload'></i>
                                            </IconButton>
                                        </label>
                                        
                                    </div>                 
                                    <div className='col-4'>                                    
                                        <div className='row button-primary'>
                                            <Button variant="contained" 
                                                onClick={processarPopulacao} 
                                                className='button-cadastrar'>
                                                Processar
                                            </Button>
                                        </div>   
                                    </div>  
                                    <div className='col-4'>                                    
                                        <div className='row button-primary'>
                                            <Button variant="outlined" className="zerar-button"
                                                onClick={zerarPopulacao} >                                                
                                                Zerar
                                            </Button>    
                                        </div>   
                                    </div>                  
                                </div>
                                <div className='upload-feedback'> 
                                    <div className="file-name-upload col-3">
                                        <label className=''>{filePopulacao.name}</label>
                                    </div>
                                    <div className="col-8">
                                        {mensagemPopulacao.tipo === 'SUCESSO' ? 
                                        <Alert onClose={limparMensagemPopulacao}
                                            severity="success">
                                            <AlertTitle>{mensagemPopulacao.mensagemRetorno}</AlertTitle>
                                        </Alert>
                                        :""}

                                        {mensagemPopulacao.tipo === 'ERRO' ? 
                                        <Alert severity="error"
                                            onClose={limparMensagemPopulacao}>
                                            <AlertTitle>{mensagemPopulacao.mensagemRetorno}</AlertTitle>
                                        </Alert>
                                        :""}
                                    </div>
                                </div>
                            </form>
                            <form className="line-upload">
                                <div className='upload'> 
                                    <label className='col-3' htmlFor="">Índice de Desenvolvimento Humano do Município:</label>
                                    <div className='col-1'>
                                        <label htmlFor="contained-button-fileIdhm">
                                            <Input accept=".csv" 
                                                id="contained-button-fileIdhm"  
                                                type="file" 
                                                name="fileIdhm"
                                                onChange={uploadIdhm}   
                                            />
                                            <IconButton variant="contained" component="span">                                            
                                                <i class='bx bx-upload'></i>
                                            </IconButton>
                                        </label>
                                        
                                    </div>                 
                                    <div className='col-4'>                                    
                                        <div className='row button-primary'>
                                            <Button variant="contained" 
                                                onClick={processarIdhm} 
                                                className='button-cadastrar'>
                                                Processar
                                            </Button>
                                        </div>   
                                    </div>     
                                    <div className='col-4'>                                    
                                        <div className='row button-primary'>
                                            <Button variant="outlined" className="zerar-button"
                                                onClick={zerarIdhm} >                                                
                                                Zerar
                                            </Button>    
                                        </div>   
                                    </div>              
                                </div>
                                <div className='upload-feedback'> 
                                    <div className="file-name-upload col-3">
                                        <label className=''>{fileIdhm.name}</label>
                                    </div>
                                    <div className="col-8">
                                        {mensagemIdhm.tipo === 'SUCESSO' ? 
                                        <Alert onClose={limparMensagemIdhm}
                                            severity="success">
                                            <AlertTitle>{mensagemIdhm.mensagemRetorno}</AlertTitle>
                                        </Alert>
                                        :""}

                                        {mensagemIdhm.tipo === 'ERRO' ? 
                                        <Alert onClose={limparMensagemIdhm}
                                            severity="error">
                                            <AlertTitle>{mensagemIdhm.mensagemRetorno}</AlertTitle>
                                        </Alert>
                                        :""}
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Uploads