import {  Link } from 'react-router-dom'
import React, { useEffect, useState } from 'react'

import Table from '../components/table/Table'
import UsuarioService from '../services/UsuarioService'
import useToken from '../useToken'

const usuariosTableHead = [
    'id',
    'Nome',
    'Email',
    'Admin',
    'Ações'
]

const renderHead = (item, index) => <th key={index}>{item}</th>

const renderBody = (item, index) => (
    <tr key={index}>
        <td>{item.idUsuario}</td>
        <td>{item.nome}</td>
        <td>{item.email}</td>
        <td>{item.admin ? 'Sim' : 'Não'}</td>
        <td>
            <Link className='bx bx-edit-alt icon-1-5rem' to={`/edit-usuario/${item.idUsuario}`} ></Link>
            <Link className='bx bxs-user-x icon-1-5rem' to={`/del-usuario/${item.idUsuario}`} ></Link>
        </td>
    </tr>
)

const Usuarios = () => {

    const [loading, setLoading] = useState(true);
    const [customerList, setCustomerList] = useState([]);
    
    const token = useToken();
  
    useEffect(() => {

        
        
      const fetchData = async () =>{
        setLoading(true);
        try {

            let header = { headers : {
                Authorization: `Bearer ${token.token}`
            }

            }
            await UsuarioService.getUsuarios(header).then(
                (response) => {
                    setCustomerList(response.data);
                }
            );
          
        } catch (error) {
          console.error(error.message);
        }
        setLoading(false);
      }
  
      fetchData();
    }, []);




    return (
        <div>
            <h2 className="page-header">
                Usuários
            </h2>
            {loading && <div>Loading</div>}

            {!loading && (
            <div className="row">
                <div className="col-12"> 
                    <div className='row button-primary'>
                        <Link to="/add-usuario" className='button-cadastrar'>Cadastrar</Link>
                    </div> 
                           
                    <div className="card">
                        <div className="card__body">
                            
                            <Table
                                limit='10'
                                headData={usuariosTableHead}
                                renderHead={(item, index) => renderHead(item, index)}
                                bodyData={customerList}
                                renderBody={(item, index) => renderBody(item, index)}
                            />
                        </div>
                    </div>
                </div>
            </div>

            )}
        </div>
    )
}

export default Usuarios
