import axios from 'axios'


const IDHM_APRENDIZAGEM_REST_API_URL = 'http://localhost:8080/api/v1/idhm';

class IdhmService {

    getIdhm(header, municipio, sort) {
        let params = ""
        if (municipio) {
            params = "?m="+municipio
        }
        if (sort) {
            let sinal = "?";
            if (municipio) {
                sinal = "&"
            }
            
            params += sinal +"sort="+sort
        }
        return axios.get(IDHM_APRENDIZAGEM_REST_API_URL+params, header);
    }

    deleteIdhm(header) {
        return axios.delete(IDHM_APRENDIZAGEM_REST_API_URL, header);
    }

}

export default new IdhmService();