import React from 'react'

import './topnav.css'

import { Link } from 'react-router-dom'

import Dropdown from '../dropdown/Dropdown'

import ThemeMenu from '../thememenu/ThemeMenu'

import user_image from '../../assets/images/foto12.png'

import user_menu from '../../assets/JsonData/user_menus.json'
import useToken from '../../useToken'



const renderUserToggle = (user) => (
    <div className="topnav__right-user">
        <div className="topnav__right-user__image">
            <img src={user.image} alt="" />
        </div>
        <div className="topnav__right-user__name">
            {user.display_name}
        </div>
    </div>
)




const renderUserMenu =(item, index, logout) => (
    <Link to='/' key={index}>
        <div className="notification-item" onClick={logout}>
            <i className={item.icon}></i>
            <span>{item.content}</span>
        </div>
    </Link>
)

const Topnav = () => {

    const logout = React.useCallback(
        () => {
            sessionStorage.removeItem('token');
            window.location.reload(false);
        }
      );

    const loginToken  = useToken().loginToken;

    if (loginToken) {
        console.log(loginToken)
    }
    const curr_user = {
        display_name: loginToken,
        image: user_image
    }

    return (
        <div className='topnav'>
            <div className='col-8'></div>
            <div className="topnav__right">
                <div className="topnav__right-item">
                    {/* dropdown here */}
                    <Dropdown
                        customToggle={() => renderUserToggle(curr_user)}
                        contentData={user_menu}
                        renderItems={(item, index) => renderUserMenu(item, index, logout)}
                    />
                </div>
                
                <div className="topnav__right-item">
                    <ThemeMenu/>
                </div>
            </div>
        </div>
    )
}

export default Topnav
