import React, { useEffect, useState } from 'react'
import UsuarioService from '../services/UsuarioService'
import { useHistory, useParams } from 'react-router-dom'
import { Link } from 'react-router-dom'
import { Checkbox } from '@mui/material'
import useToken from '../useToken'


const AddUsuario = () => {

    const [nome, setNome] = useState('')
    const [senha, setSenha] = useState('')
    const [email, setEmail] = useState('')
    const [admin, setAdmin] = useState(false)
    const history = useHistory();
    const { idUsuario } = useParams();

    const token = useToken();

    const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

    const toggle = React.useCallback(
        () => setAdmin(!admin),
        [admin, setAdmin],
      );

    let header = { headers : {
        Authorization: `Bearer ${token.token}`
            }
    }  

    const saveorUpdateUsuario = (e) => {
        e.preventDefault();

        const usuario = {nome, senha, email, admin}

        if (idUsuario) {
            UsuarioService.updateUsuario(header, idUsuario, usuario).then((response) => {
                history.push('/usuarios')
            }).catch(error => {
                console.log(error)
            })
        } else {
            UsuarioService.saveUsuario(header, usuario).then((response) => {
                console.log(response.data)
                history.push('/usuarios')
            }).catch(error => {
                console.log(error)
            })
        }

        
    }
    
    useEffect(() => {
        UsuarioService.getUsuarioById(header, idUsuario).then((response) => {
            setNome(response.data.nome)
            setEmail(response.data.email)
            setAdmin(response.data.admin)

        })
    }, [])
     
    const titulo = () => {
        if (idUsuario) {
            return <h2 className="page-header">Atualizar Usuário</h2>
        } else {
            return <h2 className="page-header">Cadastrar Usuário</h2>
        }
    }

    return (
        <div>
            {titulo()}
            <div className='container'>
                <div className='row'>
                    <div className='card col-12'>
                        <div className='card-body'>
                            <form>
                                <div className='add-usuario'>
                                    <label className='col-2' htmlFor="">Nome:</label>
                                    <div className="add-usuario_campos  col-10">
                                    <input 
                                            type="text" 
                                            name='nome' 
                                            
                                            value={nome} 
                                            onChange = {(e) => setNome(e.target.value)}  
                                        />
                                    </div>
                                </div>

                                <div className='add-usuario'>
                                    <label className='col-2' htmlFor="">Senha:</label>
                                    <div className="add-usuario_campos col-10">
                                    <input 
                                            type="password" 
                                            name='senha' 
                                            className='form-control' 
                                            value={senha} 
                                            onChange = {(e) => setSenha(e.target.value)}  
                                        />
                                    </div>
                                </div>

                                <div className='add-usuario'>
                                    <label className='col-2' htmlFor="">E-mail:</label>
                                    <div className="add-usuario_campos col-10">
                                    <input 
                                            type="text" 
                                            name='email' 
                                            className='form-control' 
                                            value={email} 
                                            onChange = {(e) => setEmail(e.target.value)}  
                                        />
                                    </div>
                                </div>
            
                                <div className='add-usuario'>
                                    <label className='col-2' htmlFor="">Administrador:</label>
                                    <div className="checkbox">
                                    <Checkbox {...label} 
                                                color="primary" 
                                                name='admin' 
                                                value={admin}
                                                onClick={toggle}  
                                            />
                                    </div>
                                </div>

                                
                                <Link className='button-cadastrar button-primary' onClick={(e) => saveorUpdateUsuario(e)}>Salvar</Link>
                                <Link to="/usuarios" className='button-cadastrar-secundary'>Cancelar</Link>
                            </form>

                        </div>
                    </div>
                </div>

            </div>
        </div>
    )
}

export default AddUsuario