import axios from 'axios'


const COTAS_APRENDIZAGEM_REST_API_URL = 'http://localhost:8080/api/v1/cotaAprendizagem';

class CotasAprendizagemService {

    getCotasAprendizagem(header,municipio) {
        let param = "";

        if (municipio) {
            param = "?m="+municipio;
        }
        return axios.get(COTAS_APRENDIZAGEM_REST_API_URL+param, header);
    }

    getCotasAprendizagemPorSegmento(header,municipio, sort) {
        let params = ""
        if (municipio) {
            params = "?m="+municipio
        }
        if (sort) {
            let sinal = "?";
            if (municipio) {
                sinal = "&"
            }
            
            params += sinal +"sort="+sort
        }
        return axios.get(COTAS_APRENDIZAGEM_REST_API_URL + "/totalPorSegmento"+params, header);
    }

    deleteCotasAprendizagem(header) {
        return axios.delete(COTAS_APRENDIZAGEM_REST_API_URL,header);
    }

}

export default new CotasAprendizagemService();
