import React, { useState } from "react";
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import logo from '../assets/images/logo-das-2.jpg'

import LoginService from "../services/LoginService";
import { Alert } from "@mui/material";

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'TCC 3 '}
      <Link color="inherit" href="https://univali.br">
        Univali 
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const theme = createTheme();

export default function SignIn({ setToken }) {

    const [mensagem, setMensagem] = useState("");

    const handleSubmit = (event) => {
        event.preventDefault();

        setMensagem("");


        const data = new FormData(event.currentTarget);
        console.log({
        email: data.get('email'),
        password: data.get('password'),
        });

        try {
            LoginService.login(data.get('email'), data.get('password')).then(
            
                (response) => {
                    console.log(response.data);
                    const token = {
                        login : response.data.login,
                        token: response.data.token
                    }
                    setToken(token)
                }
            ).catch(error => {
                console.log(error.data);
                setMensagem("Login ou senha inválidos!")
            });
            
        } catch (error) {
            console.error(error.message);
        } 





    };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <div className="sidebar__logo">
              <img src={logo} alt="company logo" />
          </div>
          
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email"
              name="email"
              autoComplete="email"
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Entrar
            </Button>
            { mensagem ? <Alert severity="error">{mensagem}</Alert> : "" }
            <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                  {/* Esqueceu sua senha? */}
                </Link>
              </Grid>
              <Grid item>
                <Link href={`./cadastrar`} variant="body2">
                  {"Não tem uma conta? Inscrever-se"}
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Copyright sx={{ mt: 8, mb: 4 }} />
      </Container>
    </ThemeProvider>
  );
}