import axios from 'axios'


const UPLOADFILE_REST_API_URL = 'http://localhost:8080/api/v1/upload';


class UploadFileService {

    uploadFile(header, formData) {
        return axios.post(UPLOADFILE_REST_API_URL, formData, header);
    }

    processarFile(fileCode, headers) {
        return axios.get(UPLOADFILE_REST_API_URL + fileCode, headers);
    }

}

export default new UploadFileService();