import axios from 'axios'


const TX_DESOCUPACAO_APRENDIZAGEM_REST_API_URL = 'http://localhost:8080/api/v1/taxaDesocupacao';

class DesocupacaoService {

    getTaxaDesocupacao(header, municipio, all, sort) {
        let params = "?all="+all;
        if (municipio) {
            params += "&m="+municipio
        }
        if (sort) {
            params += "&sort="+sort;
        }
        return axios.get(TX_DESOCUPACAO_APRENDIZAGEM_REST_API_URL+params, header);
    }

    deleteTxDesocupacao(header) {
        return axios.delete(TX_DESOCUPACAO_APRENDIZAGEM_REST_API_URL,header);
    }

}

export default new DesocupacaoService();