import axios from 'axios'
import React, {useEffect, useState} from 'react'

import Chart from 'react-apexcharts'

import { useSelector } from 'react-redux'

import StatusCard from '../components/status-card/StatusCard'


//import statusCards from '../assets/JsonData/status-card-data.json'
import CotasAprendizagemService from '../services/CotasAprendizagemService'
import DesocupacaoService from '../services/DesocupacaoService'
import IdhmService from '../services/IdhmService'
import MunicipioService from '../services/MunicipioService'
import PopulacaoService from '../services/PopulacaoService'
import useToken from '../useToken'


const Dashboard = () => {



    const [chartCotasSegmento, setChartCotasSegmento] = useState ({

        series: [
            {
                name:'Cotas de Aprendizagem',
                data: [333,444,555,666,777]
                /* data: [] */
            }
        ],
    
    
        options: {
            chart: {
              type: 'bar',
              height: 200
            },
            plotOptions: {
              bar: {
                borderRadius: 4,
                horizontal: true,
              }
            },
            dataLabels: {
              enabled: true
            },
            xaxis: {
              /* categories: [], */
              categories: ['Agricultura', 'Comércio', 'Indústria', 'Serviços', 'Transporte']
            }
        },

    });


    const [chartDesocupacaoMunicipios, setChartDesocupacaoMunicipios] = useState ({

        series: [{
            name: '15 e 17 anos',
            data: [44, 55, 41, 64, 22, 43, 21]
          }, {
            name: '18 e 24 anos',
            data: [53, 32, 33, 52, 13, 44, 32]
        }],
         
        options: {
            chart: {
              type: 'bar',
              height: 350
            },
            plotOptions: {
              bar: {
                horizontal: true,
                dataLabels: {
                  position: 'top',
                },
              }
            },
            dataLabels: {
              enabled: true,
              offsetX: -6,
              style: {
                fontSize: '12px',
                colors: ['#fff']
              }
            },
            stroke: {
              show: true,
              width: 1,
              colors: ['#fff']
            },
            tooltip: {
              shared: true,
              intersect: false
            },
            xaxis: {
                /* categories: ['Abdon Batista', 'Abelardo Luz', 'Agrolância', 'Agronômica', 'Anchieta', 'Alfredo Wagner', 'Florianópolis', 'São José', 'Criciúma'], */
                categories: []
            },
        },
       

    });

    const [chartPopulacao, setChartPopulacao] = useState ({

        series: [{
            name: 'População entre 15 e 24 anos',
            data: [400,170,200,900,360,800,300,910,600]
            /* data: [] */
        }],
        options: {
    
            chart: {
                background: 'transparent',
    
            },
            dataLabels: {
                enabled: true
            },
            stroke: {
                curve: 'smooth'
            },
            xaxis: {
                categories: ['Abdon Batista', 'Abelardo Luz', 'Agrolância', 'Agronômica', 'Anchieta', 'Alfredo Wagner', 'Florianópolis', 'São José', 'Criciúma'],
                /* categories: [], */
                labels: {
                    show: true,
                    rotate: -45,
                    rotateAlways: true,
                },
                tickPlacement: 'on'
    
            },
            legend: {
                position: 'top'
            },
            grid: {
                show: true
            }
        }

    })

    const [chartIdhm, setChartIdhm] = useState ({

        
        series: [{
            name: 'IDHM Renda',
            data: [44, 55, 41, 67, 22, 43]
            /* data: [] */
          }, {
            name: 'IDHM Educação',
            data: [13, 23, 20, 8, 13, 27]
            /* data: [] */
          }, {
            name: 'IDHM Longevidade',
            data: [11, 17, 15, 15, 21, 14]
            /* data: [] */
          }],
    
        options: {
    
            chart: {
                type: 'bar',
                height: 150,
                stacked: true,
                toolbar: {
                  show: true
                },
                zoom: {
                  enabled: true
                }
            },
            responsive: [{
                breakpoint: 480,
                options: {
                  legend: {
                    position: 'bottom',
                    offsetX: -10,
                    offsetY: 0
                  }
                }
            }],
    
            plotOptions: {
                bar: {
                  horizontal: false,
                  borderRadius: 10
                },
            },
            xaxis: {
                categories: ['Abdon Batista', 'Abelardo Luz', 'Agrolância', 'Agronômica', 'Anchieta', 'Alfredo Wagner', 'Florianópolis', 'São José', 'Criciúma'],
                /* categories: [], */
                labels: {
                    show: true,
                    rotate: -45,
                    rotateAlways: true,
                },
                tickPlacement: 'on'
            },
            legend: {
                position: 'right',
                offsetY: 20,
                
            },
            grid: {
                show: true
            },
            fill: {
                opacity: 1
            }
        }

    })


    const [statusCardCotas, setStatusCardCotas] = useState(0);

    const [statusCardMunicipios, setStatusCardMunicipios] = useState(0);

    const [statusDesocupacao_15_17, setStatusDesocupacao_15_17] = useState(0);

    const [statusDesocupacao_18_24, setStatusDesocupacao_18_24] = useState(0)

    const [municipio, setMunicipio] = useState('');

    const token = useToken();    

    const fetchDataDashboard = async () =>{    
        
        try {
            let header = { headers : {
                Authorization: `Bearer ${token.token}`
                }
            };
            let endpoints = [
                CotasAprendizagemService.getCotasAprendizagemPorSegmento(header,municipio,order),
                CotasAprendizagemService.getCotasAprendizagem(header,municipio),
                MunicipioService.getTotalMunicipios(header),
                DesocupacaoService.getTaxaDesocupacao(header,municipio, false, order),
                DesocupacaoService.getTaxaDesocupacao(header,municipio, true, order),
                PopulacaoService.getPopulacao(header,municipio,order),
                IdhmService.getIdhm(header,municipio,order)
            ];

            Promise.all(endpoints.map((endpoint) => endpoint)).then(
                axios.spread((cotasSegmento, cotasTotal, municipios, desocupacaoSc, desocupacaoAll, populacao, idhm) => {
                   console.log({ cotasSegmento, cotasTotal, municipios, desocupacaoSc, desocupacaoAll, populacao, idhm })
                   if (cotasTotal) {
                        setStatusCardCotas(cotasTotal.data)
                    }
                    if (municipios) {
                        setStatusCardMunicipios(municipios.data)
                    }
                    if (cotasSegmento) {

                        setChartCotasSegmento({
                            series: [
                                {data: cotasSegmento.data.valores}
                            ],

                            options: {
                                xaxis: {
                                  categories: cotasSegmento.data.segmentos,
                                }
                            }  
                        })
                    };
                    if (desocupacaoSc) {
                        setStatusDesocupacao_15_17(desocupacaoSc.data.tx_15_17.length > 0 ? desocupacaoSc.data.tx_15_17[0] : 0);
                        setStatusDesocupacao_18_24(desocupacaoSc.data.tx_18_24.length > 0 ? desocupacaoSc.data.tx_18_24[0] : 0);
                    };
                    if (desocupacaoAll) {
                        setChartDesocupacaoMunicipios({
                            series: [
                                {name: '15 e 17 anos',data:desocupacaoAll.data.tx_15_17}
                                ,{name: '18 e 24 anos',data:desocupacaoAll.data.tx_18_24}
                            ],
                            
                            options: {
                                xaxis: {
                                    categories: desocupacaoAll.data.municipio
                                }
                            }
                        })
                    }
                    if (populacao) {
                        let municipios = [];
                        let totalPopulacao = [];
                        populacao.data.forEach(element => {
                            municipios.push(element.municipio);
                            totalPopulacao.push(element.valorPopulacao)
                        });
                        /* console.log(populacao) */

                        setChartPopulacao({
                            series: [{
                                data: totalPopulacao
                            }],
                            options: {
                                xaxis: {
                                    categories: municipios
                                }
                            }
                        })
                    };
                    if (idhm) {
                        setChartIdhm({
                            series: [
                                {data:idhm.data.idhmRenda},
                                {data:idhm.data.idhmEducacao},
                                {data:idhm.data.idhmLongevidade}
                            ],
                            options: {
                                xaxis: {
                                    categories: idhm.data.municipios
                                }
                            }

                        })
                    }

                   
                })
                
              );

        
         
          
        } catch (error) {
          console.error(error.message);
        }

    }


    useEffect(() => {
        fetchDataDashboard();
    }, []);


    const buscarByFiltros = (e) => {
        e.preventDefault();

        fetchDataDashboard();

    };


    const orders = [{label:"nome",valor: "nome"}, {label:"maior necessidade", valor: "maior"}, {label:"menor necessidade", valor: "menor"}];

    const [order, setOrder] = useState("nome");

    const themeReducer = useSelector(state => state.ThemeReducer.mode)

    return (
        <div>
            <h2 className="page-header">Dashboard</h2>
            <form>  
                <div className='dashboard-search search-dash-card col-12'>        
                  
                    <label htmlFor="">Município</label>
                    <div className="topnav__search">
                        <input type="text"  
                        name='municipio'                     
                        value={municipio} 
                        onChange = {(e) => setMunicipio(e.target.value)}  
                        />
                        
                    </div>
                    <label htmlFor="">Ordenação</label>
                    <div className="topnav__radio">
                        
                        {orders.map(o => (
                        <>
                        <label>
                            {o.label}
                        </label>
                        <input
                            type="radio"
                            name="order"
                            value={o.valor}
                            checked={order === o.valor}
                            onChange={e => setOrder(e.currentTarget.value)}
                            />{" "}
                            
                            </>
                        ))}          
                    </div>
                   
                   <button className='search' 
                        onClick={(e) => buscarByFiltros(e)}><i class='bx bx-search'></i>
                    </button>
                   
                
                </div>
            </form>
            <div className="row">
                <div className="col-12">
                    <div className="row">

                        <div className="col-3" >
                            <StatusCard
                                icon={'bx bx-bar-chart-alt'}
                                count={statusCardCotas}
                                title={'Total de Cotas de Aprendizagem '}
                            />
                        </div>

                        <div className="col-3" >
                            <StatusCard
                                icon={'bx bxs-city'}
                                count={statusCardMunicipios}
                                title={'Total de Municípios de Santa Catarina' }
                            />
                        </div>
                        <div className="col-3" >
                            <StatusCard
                                icon={'bx bx-line-chart-down'}
                                count={statusDesocupacao_15_17 + "%"}
                                title={'Taxa de Desocupação  entre 15 a 17 anos de Santa Catarina'}
                            />
                        </div>
                        <div className="col-3" >
                            <StatusCard
                                icon={'bx bx-line-chart-down'}
                                count={statusDesocupacao_18_24 + "%"}
                                title={'Taxa de Desocupação  entre 18 a 24 anos de Santa Catarina'}
                            />
                        </div>
                    </div>
                </div>
                <div className="col-6">
                    <div className="card full-height">
                         Cotas de Aprendizagem por Segmento Econômico de Santa Catarina

                        <Chart
                            options={themeReducer === 'theme-mode-dark' ? {
                                ...chartCotasSegmento.options,
                                theme: { mode: 'dark'}
                            } : {
                                ...chartCotasSegmento.options,
                                theme: { mode: 'light'}
                            }}
                            series={chartCotasSegmento.series}
                            type='bar'
                            height={350}
                            width={700}
                        />
   
                    </div>
                </div>

                <div className="col-6">
                    <div className="card full-height">
                         Taxa de Desocupação entre 15 e 24 anos por Município    
                        <Chart
                            options={themeReducer === 'theme-mode-dark' ? {
                                ...chartDesocupacaoMunicipios.options,
                                theme: { mode: 'dark'}
                            } : {
                                ...chartDesocupacaoMunicipios.options,
                                theme: { mode: 'light'}
                            }}
                            series={chartDesocupacaoMunicipios.series}
                            type='bar'
                            height={350}
                            width={700}
                        />
                    </div>
                </div>
                <div className="col-12">
                    <div className="card full-height">
                         População entre 15 e 24 anos dos Municípios de Santa Catarina
                        <Chart
                            options={themeReducer === 'theme-mode-dark' ? {
                                ...chartPopulacao.options,
                                theme: { mode: 'dark'}
                            } : {
                                ...chartPopulacao.options,
                                theme: { mode: 'light'}
                            }}
                            series={chartPopulacao.series}
                            type='bar'
                            height={500}
                        />
                    </div>
                </div>

                <div className="col-12">
                    <div className="card full-height">
                         Índice de Desenvolvimento Humano dos Municípios de Santa Catarina
                        <Chart
                            options={themeReducer === 'theme-mode-dark' ? {
                                ...chartIdhm.options,
                                theme: { mode: 'dark'}
                            } : {
                                ...chartIdhm.options,
                                theme: { mode: 'light'}
                            }}
                            series={chartIdhm.series}
                            type='line'
                            height={500}
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Dashboard
